<template>
    <div>
        <h1>Sunrise And Sunset Time</h1>
        <div class="mainContainer">
            <div class="Container">
                <input v-model="longitude" type="text" placeholder="please enter longitude" /><br>
                <input v-model="latitude" type="text" placeholder="please enter latitude" /><br>
                <button @click="getValue">Get Result</button>
            </div>
            <div v-show="errorInstruction" class="instruction">Please Try Again</div>
            <div class="result">
                <div class="sunrise">Sunrise Time - {{sunriseResult}}</div>
                <div class="sunset">Sunset Time - {{sunsetResult}}</div>
            </div>
        </div>
        <div v-show="showLoader" class="loader">
          <img src="../assets/loader.gif" alt="">
        </div>
    </div>
</template>
<script>
export default {
    name: 'SunTime',
    data: () => ({
        longitude: null,
        latitude: null,
        sunriseResult: null,
        sunsetResult: null,
        showLoader:false,
        errorInstruction:false,
    }),
    methods: {
        getValue: function() {
            var longitude = this.longitude
            var latitude = this.latitude
            this.showLoader = true
            fetch(`https://api.sunrise-sunset.org/json?lat=${latitude}&lng=${longitude}&formatted=0`)
                .then((response) => { return response.json() })
                .then((response) => {
                        console.log(response)
                        var date1 = new Date(response.results.sunrise)
                        var date2 = new Date(response.results.sunset)
                        var sunrise = date1.toLocaleTimeString();
                        var sunset = date2.toLocaleTimeString();
                     this.sunriseResult = sunrise 
                     this.sunsetResult = sunset

                })
                .catch((error)=>{
                  this.errorInstruction = true
                })
                .finally(()=>{
                  this.showLoader = false
                })
    }
}

}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
