    margin: 40px 0 0;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}

input,
button {
    margin-bottom: 50px;

}

.mainContainer {
    display: flex;
    justify-content: space-around;
    margin-top: 75px;

}

input {
    height: 30px;
    font-size: 15px;
}

.result {
    font-size: 20px;
    font-size: bold;
}

button {}

h1 {}
.loader{
  height: 100vh;
  width:100vw;
  position: fixed;
  top:0;
  left:0;
  background-color: rgba(255,255,255,0.6);
  display: flex;
  justify-content: center;
  align-items: center;

}
.instruction{
  color:red;
}
</style>